import axios from 'axios';

const instance = axios.create({
  baseURL: process.env.VUE_APP_API_URL,
});

function getSpatialList(url) {
  return instance.post(`spatialList?${url}`);
}

function getSelectZoneList(url) {
  return instance.post(`spatialList?${url}`);
}

function getSensorList(url, page) {
  return instance.post(`sensorList?${url}&page=${page}`);
}

function getLocationCode() {
  return instance.get('areaList');
}

function getLearningList(url, page) {
  return instance.post(`learningList?${url}&page=${page}`);
}

function getLdmList() {
  return instance.get('ldmList');
}

export {
  getLocationCode,
  getSensorList,
  getLearningList,
  getSpatialList,
  getSelectZoneList,
  getLdmList,
};
